.container {
  position: relative;
  margin-right: auto;
  margin-left: auto;
  padding: 0 70px;
  box-sizing: border-box;
  transition: all 0.2s linear;

  &_flex {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include breakpoint(max-width 750px) {
      flex-wrap: wrap;
    }
  }

  &_row {
    display: flex;
    align-items: flex-start;
    margin-top: 50px;

    &-center {
      align-items: center;
    }

    &-first {
      margin-top: 30px;
    }

    @include breakpoint(max-width 1140px) {
      flex-wrap: wrap;
      margin-top: 0;
    }
  }

  &_wrap {
    flex-wrap: wrap;
  }

  &_tablet {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
  }

  &_for-biggest {
    margin-top: 0;
  }

  &_show-more {
    margin-top: 0;
  }
}

@media (max-width: 750px) {
  .container {
    width: 100%;
    padding: 0 20px;
  }
}

@media (min-width: 750px) {
  .container {
    width: 100%;
  }
}

@media (min-width: 1440px) {
  .container {
    width: 1440px;
  }
}

.case-block {
  &__sm {
    width: 30.77% !important;
    transition: all 0.2s linear;

    @include breakpoint(max-width 1140px) {
      width: 100% !important;
      margin-top: 50px;
    }

    @include breakpoint(max-width 750px) {
      margin-top: 30px;
    }

    img {
      width: 100%;
    }

    &_tablet {
      @include breakpoint(max-width 1140px) {
        width: calc(50% - 25px) !important;
        //margin-top: 0;

        &:first-child {
          margin-right: 50px;
        }
      }

      @include breakpoint(max-width 750px) {
        width: 100% !important;
        margin-top: 30px;
        margin-right: 0;

        &:first-child {
          margin-right: 0;
        }
      }
    }

    &_project-first {
      position: relative;
      top: 66px;

      @include breakpoint(max-width 1140px) {
        position: static;
      }
    }
  }

  &__m {
    width: 48.08% !important;
    transition: all 0.2s linear;

    img {
      width: 100%;
    }

    @include breakpoint(max-width 1140px) {
      width: 100% !important;
      margin-top: 50px;
    }

    @include breakpoint(max-width 750px) {
      margin-top: 30px;
    }
  }

  &__l {
    width: 65.385% !important;
    transition: all 0.2s linear;

    img {
      width: 100%;
    }

    @include breakpoint(max-width 1140px) {
      width: 100% !important;
      margin-top: 50px;
    }

    @include breakpoint(max-width 750px) {
      margin-top: 30px;
    }
  }

  &__xl {
    width: 100% !important;
    transition: all 0.2s linear;

    img {
      width: 100%;
    }

    @include breakpoint(max-width 1140px) {
      margin-top: 50px;
    }

    @include breakpoint(max-width 750px) {
      margin-top: 30px;
    }

    &_flex {
      display: flex;

      @include breakpoint(max-width 1140px) {
        flex-wrap: wrap;
      }
    }
  }
}

.mr50 {
  margin-right: 50px;

  &:nth-child(2n) {
    margin-right: 0;
  }

  @include breakpoint(max-width 1140px) {
    margin-right: 0;
  }
}